<template>
  <div class="search">
    <div class="search-box-wrapper">
      <searchbox @search="searchHandler" ref="searchBox"></searchbox>
    </div>
    <div ref="shortcutWrapper" class="shortcut-wrapper" v-if="!query">
      <scroll class="shortcut"  :data="searchHistory">
        <div>
          <div class="hot-key">
            <h1 class="title">热门搜索</h1>
            <ul>
              <li v-for="(item, i) in hotKeys" :key="i" class="item">
                <span @click="searchHot(item.k)" v-html="item.k"></span>
              </li>
            </ul>
          </div>
          <div class="search-history" v-if="searchHistory.length">
            <h1 class="title">
              <span class="text">搜索历史</span>
              <span class="clear" @click="deleteAll">
                <i class="icon-clear"></i>
              </span>
            </h1>
            <searchList :searches="searchHistory" @select="searchHot" @delete="deleteOne"></searchList>
          </div>
        </div>
      </scroll>
    </div>
    <div class="search-result" ref="searchResult" v-show="query">
      <suggest :query="query" :showSinger="showSinger" @listScroll="inputBlur"></suggest>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import scroll from '@/base/scroll/scroll'
import localforage from 'localforage'
import searchbox from '@/base/searchbox/searchbox'
import searchList from '@/base/search-list/search-list'
import suggest from '@/components/suggest/suggest'
import {getHotKey} from 'api/searchKey'

export default {
  components: {
    searchbox,
    searchList,
    suggest,
    scroll
  },
  data() {
    return {
      query: '',
      showSinger: true,
      hotKeys: [],
      searchHistory: []
    }
  },
  created() {
    this._getHotKey()
    localforage.getItem('_music_history').then((val) => {
      this.searchHistory = val
    })
  },
  methods: {
    inputBlur() {
      this.$refs.searchBox.$refs.query.blur()
    },
    searchHandler(query) {
      this.query = query
      if (!query) {
        return
      }
      this.upateSearchHistory(query)
    },
    searchHot(hotKey) {
      this.query = hotKey
      this.$refs.searchBox.setVal(hotKey)
    },
    upateSearchHistory(key) {
      let _index = this.searchHistory.indexOf(key)
      if (_index > -1) {
        this.searchHistory.splice(_index, 1)
      }
      this.searchHistory.push(key)
    },
    deleteOne(key) {
      let _index = this.searchHistory.indexOf(key)
      this.searchHistory.splice(_index, 1)
    },
    deleteAll() {
      this.searchHistory = []
    },
    _getHotKey() {
      getHotKey().then((data) => {
        this.hotKeys = data.data.data.hotkey.slice(0, 9)
      })
    }
  },
  watch: {
    query(newVal) {
      if (!newVal) {
        return false
      }
    },
    searchHistory(newArray) {
      localforage.setItem('_music_history', newArray)
    }
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .search-box-wrapper
      margin: 20px
    .shortcut-wrapper
      position: fixed
      top: 178px
      bottom: 0
      width: 100%
      .shortcut
        height: 100%
        overflow: hidden
        .hot-key
          margin: 0 20px 20px 20px
          .title
            margin-bottom: 20px
            font-size: $font-size-medium
            color: $color-text-l
          .item
            display: inline-block
            padding: 5px 10px
            margin: 0 20px 10px 0
            border-radius: 6px
            background: $color-highlight-background
            font-size: $font-size-medium
            color: $color-text-d
        .search-history
          position: relative
          margin: 0 20px
          .title
            display: flex
            align-items: center
            height: 40px
            font-size: $font-size-medium
            color: $color-text-l
            .text
              flex: 1
            .clear
              extend-click()
              .icon-clear
                font-size: $font-size-medium
                color: $color-text-d
    .search-result
      position: fixed
      width: 100%
      top: 178px
      bottom: 0
</style>


