<template>
  <div class="rank" ref="rank">
    <Scroll :data="topList" class="toplist" ref="toplist">
      <ul>
        <li @click="selectItem(item)" class="item" v-for="(item, k) in topList" :key="k">
          <div class="icon">
            <img width="100" height="100" v-lazy="item.picUrl"/>
          </div>
          <ul class="songlist">
            <li class="song" v-for="(song,index) in item.songList" :key="index">
              <span>{{index + 1}}</span>
              <span>{{song.songname}}-{{song.singername}}</span>
            </li>
          </ul>
        </li>
      </ul>
      <div class="loading-container" v-show="!topList.length">
      </div>
      <div class="loading-container" v-if="!topList.length">
        <Loading></Loading>
      </div>
    </Scroll>
    <router-view></router-view>
  </div>
</template>
<script>
import {mapMutations} from 'vuex'
import Scroll from '@/base/scroll/scroll'
import Loading from '@/base/loading/loading'
import {getRank} from 'api/getRank'
import {ERROR_OK} from 'api/config'
export default {
  components: {
    Scroll,
    Loading
  },
  data() {
    return {
      topList: []
    }
  },
  created() {
    this._getRank()
  },
  methods: {
    _getRank() {
      getRank().then((data) => {
        if (data.data.code === ERROR_OK) {
          this.topList = data.data.data.topList
        }
      })
    },
    selectItem(item) {
      this.$router.push('/rank/' + item.id)
      this.setTopinfo(item)
    },
    ...mapMutations({
      setTopinfo: 'SET_TOPINFO'
    })
  }
}
</script>
<style scoped lang="stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"

  .rank
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .toplist
      height: 100%
      overflow: hidden
      .item
        display: flex
        margin: 0 20px
        padding-top: 20px
        height: 100px
        &:last-child
          padding-bottom: 20px
        .icon
          flex: 0 0 100px
          width: 100px
          height: 100px
        .songlist
          flex: 1
          display: flex
          flex-direction: column
          justify-content: center
          padding: 0 20px
          height: 100px
          overflow: hidden
          background: $color-highlight-background
          color: $color-text-d
          font-size: $font-size-small
          .song
            no-wrap()
            line-height: 26px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>


